import React, { useEffect, useRef, useState } from 'react';
import { FaPhone, FaStar, FaCheckCircle, FaArrowRight, FaShieldAlt, FaHome } from 'react-icons/fa';
import './hero.css';
import { Profile } from '../../content/content';
import { navigate } from 'gatsby';
import { useGoogleForm } from '../../hooks/useGoogleForm'; //  use your existing hook

const Hero: React.FC = () => {
  const [isVisible, setIsVisible] = useState(false);
  const heroRef = useRef<HTMLDivElement>(null);

  const { formData, handleChange, submit, isSubmitting, submitSuccess } = useGoogleForm('legacy-exteriors-lead');

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => entry.isIntersecting && setIsVisible(true),
      { threshold: 0.1 }
    );
    if (heroRef.current) observer.observe(heroRef.current);
    return () => heroRef.current && observer.unobserve(heroRef.current);
  }, []);

  const trustBadges = [
    { icon: FaShieldAlt, text: 'Fully Insured' },
    { icon: FaCheckCircle, text: '25+ Years Experience' },
    { icon: FaHome, text: 'Local & Family Owned' },
  ];

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    submit({ message: formData.message || 'Service inquiry via Hero form' });
  };

  return (
    <section className="hero" ref={heroRef} id="home">
      <div className="hero__background">
        <div className="hero__image" />
        <div className={`hero__overlay ${isVisible ? 'hero__overlay--animated' : ''}`} />
      </div>

      <div className="hero__container">
        <div className="hero__layout">
          <div className="hero__content">
            <h1 className={`hero__headline ${isVisible ? 'hero__headline--animated' : ''}`}>
              Premium Exterior Solutions
              <span className="hero__headline-accent"> for Your Home</span>
            </h1>

            <p className={`hero__subtext ${isVisible ? 'hero__subtext--animated' : ''}`}>
              Transform your home's exterior with our expert roofing, siding, and door installation services. 
              We combine superior craftsmanship with premium materials to deliver lasting beauty and protection 
              for your most valuable investment.
            </p>

            <div className={`hero__buttons ${isVisible ? 'hero__buttons--animated' : ''}`}>
              <button className="hero__cta hero__cta--primary" onClick={() => navigate('/Contact')}>
                <span>Get Free Estimate</span>
                <FaArrowRight className="hero__cta-icon" />
              </button>

              {/* <a className="topbar__contact-link" href={`tel:${Profile.phone}`}>{Profile.phone}</a> */}


              <button className="hero__cta hero__cta--secondary" onClick={()=>navigate(`tel:${Profile.phone}`)}>
                <FaPhone className="hero__cta-icon" />
                <span>{`${Profile.phone}`}</span>
              </button>
            </div>

            <div className={`hero__trust ${isVisible ? 'hero__trust--animated' : ''}`}>
              <div className="hero__trust-badges">
                {trustBadges.map((badge, index) => {
                  const Icon = badge.icon;
                  return (
                    <div key={index} className="hero__trust-badge">
                      <Icon className="hero__trust-icon" />
                      <span className="hero__trust-text">{badge.text}</span>
                    </div>
                  );
                })}
              </div>
              <div className="hero__rating">
                <div className="hero__stars">
                  {[...Array(5)].map((_, index) => (
                    <FaStar key={index} className="hero__star" />
                  ))}
                </div>
                <span className="hero__rating-text">500+ Happy Customers</span>
              </div>
            </div>
          </div>

          {/* Contact Form */}
          <div className={`hero__form-container ${isVisible ? 'hero__form-container--animated' : ''}`}>
            <div className="hero__form">
              <div className="hero__form-header">
                <h3 className="hero__form-title">Free Quote</h3>
                <p className="hero__form-subtitle">Get started with your project</p>
              </div>
              
              <form className="hero__form-content" onSubmit={handleSubmit}>
                <div className="hero__form-row">
                  <input
                    type="text"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    placeholder="Full Name"
                    className="hero__form-input"
                    required
                  />
                </div>

                <div className="hero__form-row hero__form-row--two">
                  <input
                    type="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    placeholder="Email Address"
                    className="hero__form-input"
                    required
                  />
                  <input
                    type="tel"
                    name="phone"
                    value={formData.phone}
                    onChange={handleChange}
                    placeholder="Phone Number"
                    className="hero__form-input"
                    required
                  />
                </div>

                <div className="hero__form-row">
                  <select
                    className="hero__form-input hero__form-select"
                    onChange={e =>
                      handleChange({ target: { name: 'message', value: e.target.value } } as any)
                    }
                    required
                  >
                    <option value="">Service Needed</option>
                    <option value="Roofing">Roofing</option>
                    <option value="Siding">Siding</option>
                    <option value="Windows">Windows</option>
                    <option value="Gutters">Gutters</option>
                    <option value="Consultation">Free Consultation</option>
                  </select>
                </div>

                <div className="hero__form-row">
                  <textarea
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    placeholder="Project details..."
                    className="hero__form-input hero__form-textarea"
                    rows={3}
                  ></textarea>
                </div>

                {/*  Button handles all states */}
                <button
                  type="submit"
                  className="hero__form-submit"
                  disabled={isSubmitting || submitSuccess}
                >
                  {isSubmitting ? (
                    <>
                      <span className="spinner" style={{
                        display: 'inline-block',
                        width: '14px',
                        height: '14px',
                        border: '2px solid #fff',
                        borderTopColor: 'transparent',
                        borderRadius: '50%',
                        marginRight: '8px',
                        animation: 'spin 1s linear infinite'
                      }} />
                      Sending...
                    </>
                  ) : submitSuccess ? (
                    ' Thank you!'
                  ) : (
                    <>
                      Get Free Quote
                      <FaArrowRight className="hero__form-submit-icon" />
                    </>
                  )}
                </button>

                <p className="hero__form-disclaimer">
                  No obligation  Free consultation  Quick response
                </p>
              </form>
            </div>
          </div>
        </div>
      </div>

      <div className="hero__scroll-indicator">
        <div className="hero__scroll-arrow" />
      </div>

      {/*  Simple spinner keyframe */}
      <style>{`
        @keyframes spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }
      `}</style>
    </section>
  );
};

export default Hero;
