import React, { useState, useRef, useEffect } from 'react';
import { FaCheckCircle, FaShieldAlt, FaAward, FaHome, FaStar, FaChartLine, FaHandshake, FaUsers, FaMedal } from 'react-icons/fa';
import './TrustSection.css';
import { navigate } from 'gatsby';

const TrustSection: React.FC = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [counters, setCounters] = useState({
    years: 0,
    projects: 0,
    clients: 0,
    warranty: 0
  });
  const sectionRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          animateCounters();
        }
      },
      { threshold: 0.3 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => {
      if (sectionRef.current) {
        observer.unobserve(sectionRef.current);
      }
    };
  }, []);

  const animateCounters = () => {
    const duration = 2000;
    const steps = 60;
    const stepDuration = duration / steps;

    const targetValues = {
      years: 25,
      projects: 100,
      clients: 99,
      warranty: 25
    };

    Object.keys(targetValues).forEach(key => {
      let current = 0;
      const target = targetValues[key as keyof typeof targetValues];
      const increment = target / steps;

      const timer = setInterval(() => {
        current += increment;
        if (current >= target) {
          current = target;
          clearInterval(timer);
        }
        setCounters(prev => ({
          ...prev,
          [key]: Math.floor(current)
        }));
      }, stepDuration);
    });
  };

  const trustPoints = [
    {
      icon: FaChartLine,
      title: '25+ Years Expertise',
      description: 'Decades of trusted experience in exterior construction and home improvement'
    },
    {
      icon: FaShieldAlt,
      title: 'Fully Licensed & Insured',
      description: 'Complete protection and peace of mind with fully licensed and insured professionals'
    },
    {
      icon: FaAward,
      title: 'A+ BBB Rated',
      description: 'Highest rating for integrity, quality workmanship, and customer satisfaction'
    },
    {
      icon: FaMedal,
      title: 'Premium Materials',
      description: 'Only the highest quality materials designed to withstand Idaho\'s climate'
    },
    {
      icon: FaUsers,
      title: 'Satisfaction Guaranteed',
      description: 'We stand behind our work with a commitment to your complete satisfaction'
    },
    {
      icon: FaCheckCircle,
      title: '25-Year Warranty',
      description: 'Comprehensive warranty protection on all workmanship and materials'
    }
  ];

  const stats = [
    { value: `${counters.years}+`, label: 'Years Experience', icon: FaChartLine },
    { value: `${counters.projects}+`, label: 'Projects Completed', icon: FaHome },
    { value: `${counters.clients}%`, label: 'Satisfied Clients', icon: FaHandshake },
    { value: `${counters.warranty} Year`, label: 'Warranty', icon: FaShieldAlt }
  ];

  return (
    <section className="trust-section" ref={sectionRef} id="why-choose-us">
      <div className="trust-section__container">
        {/* Section Header */}
        <div className="trust-section__header">
          <div className="trust-section__badge">
            <FaStar className="trust-section__badge-icon" />
            Why Choose Legacy
          </div>
          <h2 className="trust-section__title">
            Trusted by Idaho Homeowners
            <span className="trust-section__title-accent"> for Over 25 Years</span>
          </h2>
          <p className="trust-section__subtitle">
            Experience the Legacy differencewhere quality craftsmanship, premium materials, 
            and unwavering commitment to excellence come together to protect and enhance your home.
          </p>
        </div>

        {/* Stats Counter */}
        <div className={`trust-section__stats ${isVisible ? 'trust-section__stats--visible' : ''}`}>
          {stats.map((stat, index) => {
            const IconComponent = stat.icon;
            return (
              <div key={index} className="trust-stat">
                <div className="trust-stat__icon">
                  <IconComponent />
                </div>
                <div className="trust-stat__content">
                  <div className="trust-stat__value">{stat.value}</div>
                  <div className="trust-stat__label">{stat.label}</div>
                </div>
              </div>
            );
          })}
        </div>

        {/* Trust Points Grid */}
        <div className="trust-section__grid">
          {trustPoints.map((point, index) => {
            const IconComponent = point.icon;
            return (
              <div
                key={index}
                className={`trust-card ${isVisible ? 'trust-card--visible' : ''}`}
                style={{ animationDelay: `${index * 100}ms` }}
              >
                <div className="trust-card__content">
                  <div className="trust-card__icon-container">
                    <div className="trust-card__icon-bg"></div>
                    <IconComponent className="trust-card__icon" />
                  </div>

                  <div className="trust-card__text">
                    <h3 className="trust-card__title">{point.title}</h3>
                    <p className="trust-card__description">{point.description}</p>
                  </div>
                </div>
              </div>
            );
          })}
        </div>

        {/* CTA Section */}
        <div className={`trust-section__cta ${isVisible ? 'trust-section__cta--visible' : ''}`}>
          <div className="trust-cta">
            <div className="trust-cta__content">
              <FaAward className="trust-cta__icon" />
              <div className="trust-cta__text">
                <h3 className="trust-cta__title">Ready to Experience Excellence?</h3>
                <p className="trust-cta__description">
                  Join thousands of satisfied homeowners who trust Bullseye Exteriors with their most important investment.
                </p>
              </div>
            </div>
            <div className="trust-cta__actions">
              <button className="trust-cta__button trust-cta__button--primary"  onClick={()=>navigate('/Contact')}>
                Get Your Free Estimate
              </button>
              <button className="trust-cta__button trust-cta__button--secondary">
                View Our Work
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default TrustSection;