import React, { useState, useRef, useEffect } from 'react';
import { FaHammer, FaHome, FaDoorOpen, FaArrowRight, FaShieldAlt, FaStar, FaCheckCircle, FaWindowMaximize } from 'react-icons/fa';
import { GiStoneWall } from 'react-icons/gi';
import './services.css';



 
const Services: React.FC = () => {
  const [isVisible, setIsVisible] = useState(false);
  const sectionRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.2 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => {
      if (sectionRef.current) {
        observer.unobserve(sectionRef.current);
      }
    };
  }, []);

  const services = [
    {
      id: 1,
      icon: FaHammer,
      title: 'Professional Roofing',
      subtitle: 'Durable & Weather-Resistant',
      description: 'Expert roofing solutions designed to withstand Idaho\'s toughest weather conditions. We use premium materials and proven techniques for lasting protection.',
      features: ['25+ Year Warranty', 'Weather Resistant', 'Energy Efficient', 'Local Code Compliant'],
      cta: 'Explore Roofing',
      gradient: 'gradient-1',
      delay: 0
    },
    {
      id: 2,
      icon: GiStoneWall,
      title: 'Premium Siding',
      subtitle: 'Beautiful & Climate-Tough',
      description: 'Transform your home\'s exterior with siding solutions built to endure harsh climates while enhancing curb appeal and property value.',
      features: ['Climate Resistant', 'Low Maintenance', 'Premium Materials', 'Custom Colors'],
      cta: 'View Siding Options',
      gradient: 'gradient-2',
      delay: 200
    },
    // {
    //   id: 3,
    //   icon: FaDoorOpen,
    //   title: 'Custom Doors',
    //   subtitle: 'Secure & Energy Efficient',
    //   description: 'Enhance your home\'s security and efficiency with beautifully crafted doors that combine style, durability, and superior insulation.',
    //   features: ['Energy Star Rated', 'Enhanced Security', 'Custom Designs', 'Professional Installation'],
    //   cta: 'Discover Doors',
    //   gradient: 'gradient-3',
    //   delay: 400
    // }
    {
        id: 3,
        icon: FaWindowMaximize,
        title: 'Custom Windows',
        subtitle: 'Bright, Secure & Energy Efficient',
        description: 'Transform your home with beautifully designed windows that deliver exceptional energy efficiency, lasting durability, and enhanced comfort year-round.',
        features: ['Energy Star Rated', 'Advanced Security', 'Custom Sizes & Styles', 'Professional Installation'],
        cta: 'Discover Windows',
        gradient: 'gradient-3',
        delay: 400
    }

  ];

  return (
    <section className="services" ref={sectionRef} id="services">
      <div className="services__container">
        {/* Section Header */}
        <div className="services__header">
          <div className="services__badge">
            <FaStar className="services__badge-icon" />
            Our Premium Services
          </div>
          <h2 className="services__title">
            Expert Exterior Solutions
            <span className="services__title-accent"> for Idaho Homes</span>
          </h2>
          <p className="services__subtitle">
            Professional exterior services that combine superior craftsmanship with premium materials 
            to protect and enhance your home's value and beauty for decades to come.
          </p>
        </div>

        {/* Services Grid */}
        <div className="services__grid">
          {services.map((service) => {
            const IconComponent = service.icon;
            return (
              <div
                key={service.id}
                className={`service-card ${isVisible ? 'service-card--visible' : ''}`}
                style={{ animationDelay: `${service.delay}ms` }}
              >
                {/* Card Background Gradient */}
                <div className={`service-card__gradient ${service.gradient}`}></div>
                
                {/* Card Content */}
                <div className="service-card__content">
                  {/* Icon Container */}
                  <div className="service-card__icon-container">
                    <div className="service-card__icon-bg"></div>
                    <IconComponent className="service-card__icon" />
                  </div>

                  {/* Title & Subtitle */}
                  <div className="service-card__header">
                    <h3 className="service-card__title">{service.title}</h3>
                    <p className="service-card__subtitle">{service.subtitle}</p>
                  </div>

                  {/* Description */}
                  <p className="service-card__description">{service.description}</p>

                  {/* Features List */}
                  <ul className="service-card__features">
                    {service.features.map((feature, index) => (
                      <li key={index} className="service-card__feature">
                        <FaCheckCircle className="service-card__feature-icon" />
                        <span>{feature}</span>
                      </li>
                    ))}
                  </ul>

                  {/* CTA Button */}
                  <button className="service-card__cta">
                    <span className="service-card__cta-text">{service.cta}</span>
                    <div className="service-card__cta-icon">
                      <FaArrowRight />
                    </div>
                    <div className="service-card__cta-overlay"></div>
                  </button>
                </div>

                {/* Hover Effect Element */}
                <div className="service-card__hover"></div>
              </div>
            );
          })}
        </div>

        {/* Trust Footer */}
     
      </div>
    </section>
  );
};

export default Services;