import React, { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay } from 'swiper/modules';
import { FaArrowLeft, FaArrowRight, FaHome, FaStar } from 'react-icons/fa';
import { MdLocationOn, MdDateRange } from 'react-icons/md';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import './project.css';

interface Project {
  id: number;
  title: string;
  location: string;
  description: string;
  category: string;
  duration: string;
  image: string;
  rating: number;
}

const ProjectShowcase: React.FC = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const navigationPrevRef = useRef<HTMLButtonElement>(null);
  const navigationNextRef = useRef<HTMLButtonElement>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);

  const projects: Project[] = [
    {
      id: 1,
      title: 'Modern Roof Replacement',
      location: 'Boise, ID',
      description: 'Complete roof replacement with premium architectural shingles and enhanced ventilation system for optimal performance and durability.',
      category: 'Roofing',
      duration: '2 Weeks',
      image: 'https://images.unsplash.com/photo-1591955506264-3f5a6834570a?ixlib=rb-4.0.3&auto=format&fit=crop&w=1200&q=80',
      rating: 5
    },
    {
      id: 2,
      title: 'Premium Siding Installation',
      location: 'Nampa, ID',
      description: 'Fiber cement siding installation with integrated insulation for superior energy efficiency and modern aesthetic appeal.',
      category: 'Siding',
      duration: '3 Weeks',
      image: 'https://images.unsplash.com/photo-1573496359142-b8d87734a5a2?ixlib=rb-4.0.3&auto=format&fit=crop&w=1200&q=80',
      rating: 5
    },
    {
      id: 3,
      title: 'Complete Exterior Remodel',
      location: 'Caldwell, ID',
      description: 'Full exterior transformation including new roofing, siding, and windows for a complete home renewal.',
      category: 'Full Remodel',
      duration: '6 Weeks',
      image: 'https://images.unsplash.com/photo-1613490493576-7fde63acd811?ixlib=rb-4.0.3&auto=format&fit=crop&w=1200&q=80',
      rating: 5
    },
    {
      id: 4,
      title: 'Luxury Roof Restoration',
      location: 'Eagle, ID',
      description: 'Historic home roof restoration using traditional methods and premium materials to preserve architectural integrity.',
      category: 'Roofing',
      duration: '4 Weeks',
      image: 'https://images.unsplash.com/photo-1564013799919-ab600027ffc6?ixlib=rb-4.0.3&auto=format&fit=crop&w=1200&q=80',
      rating: 5
    },
    {
      id: 5,
      title: 'Contemporary Siding Solution',
      location: 'Kuna, ID',
      description: 'Modern vertical siding installation with contrasting accents and professional finishing for standout street presence.',
      category: 'Siding',
      duration: '2 Weeks',
      image: 'https://images.unsplash.com/photo-1583608205776-bfd35f0d9f83?ixlib=rb-4.0.3&auto=format&fit=crop&w=1200&q=80',
      rating: 5
    }
  ];

  const openModal = (project: Project) => {
    setSelectedProject(project);
    setIsModalOpen(true);
    document.body.style.overflow = 'hidden';
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedProject(null);
    document.body.style.overflow = 'unset';
  };

  return (
    <section className="project-showcase" id="projects">
      <div className="project-showcase__container">
        {/* Section Header */}
        <div className="project-showcase__header">
          <div className="project-showcase__badge">
            <FaStar className="project-showcase__badge-icon" />
             Portfolio
          </div>
          <h2 className="project-showcase__title">
             Our Feature
            <span className="project-showcase__title-accent"> Projects</span>
          </h2>
          <p className="project-showcase__subtitle">
            Discover how we've transformed homes across Idaho with our premium exterior solutions 
            and exceptional craftsmanship.
          </p>
        </div>

        {/* Swiper Slider */}
        <div className="project-showcase__slider">
          <Swiper
            modules={[Navigation, Pagination, Autoplay]}
            spaceBetween={30}
            slidesPerView={1}
            breakpoints={{
              640: {
                slidesPerView: 1,
                spaceBetween: 20
              },
              768: {
                slidesPerView: 2,
                spaceBetween: 30
              },
              1024: {
                slidesPerView: 3,
                spaceBetween: 30
              }
            }}
            navigation={{
              prevEl: navigationPrevRef.current,
              nextEl: navigationNextRef.current,
            }}
            pagination={{
              clickable: true,
            }}
            autoplay={{
              delay: 5000,
              disableOnInteraction: false,
            }}
            speed={600}
            loop={true}
            onSlideChange={(swiper) => setActiveIndex(swiper.realIndex)}
            onSwiper={(swiper) => {
              setTimeout(() => {
                if (swiper.params.navigation) {
                  (swiper.params.navigation as any).prevEl = navigationPrevRef.current;
                  (swiper.params.navigation as any).nextEl = navigationNextRef.current;
                }
                swiper.navigation.init();
                swiper.navigation.update();
              });
            }}
            className="project-swiper"
          >
            {projects.map((project) => (
              <SwiperSlide key={project.id}>
                <div className="project-card">
                  {/* Image Container */}
                  <div className="project-card__image-container">
                    <img 
                      src={project.image} 
                      alt={project.title}
                      className="project-card__image"
                    />
                    <div className="project-card__badge">
                      {project.category}
                    </div>
                  </div>

                  {/* Card Content */}
                  <div className="project-card__content">
                    <h3 className="project-card__title">{project.title}</h3>
                    <p className="project-card__description">{project.description}</p>
                    
                    <div className="project-card__meta">
                      <div className="project-card__location">
                        <MdLocationOn />
                        {project.location}
                      </div>
                      <div className="project-card__duration">
                        <MdDateRange />
                        {project.duration}
                      </div>
                    </div>

                    <div className="project-card__rating">
                      {[...Array(project.rating)].map((_, i) => (
                        <FaStar key={i} className="project-card__star" />
                      ))}
                    </div>

                    <button 
                      className="project-card__expand"
                      onClick={() => openModal(project)}
                    >
                      View Project Details
                    </button>
                  </div>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>

          {/* Custom Navigation */}
          <div className="project-showcase__navigation">
            <button 
              ref={navigationPrevRef}
              className="project-showcase__nav-button project-showcase__nav-button--prev"
              aria-label="Previous project"
            >
              <FaArrowLeft />
            </button>
            
            <div className="project-showcase__pagination">
              <span className="project-showcase__pagination-current">
                {(activeIndex % projects.length) + 1}
              </span>
              <span className="project-showcase__pagination-divider">/</span>
              <span className="project-showcase__pagination-total">
                {projects.length}
              </span>
            </div>
            
            <button 
              ref={navigationNextRef}
              className="project-showcase__nav-button project-showcase__nav-button--next"
              aria-label="Next project"
            >
              <FaArrowRight />
            </button>
          </div>
        </div>

        {/* CTA Section */}
      
      </div>

      {/* Project Modal */}
      {isModalOpen && selectedProject && (
        <div className="project-modal">
          <div className="project-modal__overlay" onClick={closeModal}></div>
          <div className="project-modal__content">
            <button 
              className="project-modal__close"
              onClick={closeModal}
              aria-label="Close modal"
            >
              &times;
            </button>
            
            <img 
              src={selectedProject.image} 
              alt={selectedProject.title}
              className="project-modal__image"
            />
            
            <div className="project-modal__info">
              <div className="project-modal__header">
                <div className="project-modal__category">{selectedProject.category}</div>
                <h2 className="project-modal__title">{selectedProject.title}</h2>
                <div className="project-modal__location">
                  <MdLocationOn />
                  {selectedProject.location}
                </div>
              </div>
              
              <p className="project-modal__description">{selectedProject.description}</p>
              
              <div className="project-modal__details">
                <div className="project-modal__detail">
                  <strong>Project Duration</strong>
                  <span>{selectedProject.duration}</span>
                </div>
                <div className="project-modal__detail">
                  <strong>Customer Rating</strong>
                  <div className="project-modal__rating">
                    {[...Array(selectedProject.rating)].map((_, i) => (
                      <FaStar key={i} className="project-modal__star" />
                    ))}
                  </div>
                </div>
              </div>
              
              <button className="project-modal__cta">
                Get a Free Quote for Similar Project
              </button>
            </div>
          </div>
        </div>
      )}
    </section>
  );
};

export default ProjectShowcase;